
import { computed } from 'vue'
import type { PropType } from 'vue'
const typeArray = ['default', 'primary', 'success', 'info', 'warning', 'danger']
const positionArray = ['left', 'right']

type IButtonType = PropType<
  'default' | 'primary' | 'success' | 'info' | 'warning' | 'danger'
>
type IButtonPosi = PropType<'left' | 'right'>
interface IButtonProps {
  type: string
  icon?: string
  loading: boolean
  position: string
  disabled: boolean
}
export default {
  name: 'AxeButton', // namenameaxe-<axe-button>
  props: {
    // size // TODO: buttonsize
    type: {
      type: String as IButtonType,
      default: 'default',
      validator(type: string) {
        if (!typeArray.includes(type)) {
          throw Error(
            'type' +
              typeArray.join('') +
              ''
          )
        }
        return true
      }
    },
    icon: String,
    loading: Boolean,
    position: {
      type: String as IButtonPosi,
      default: 'left',
      require: false,
      validator(type: string) {
        if (!positionArray.includes(type)) {
          throw Error(
            `position${positionArray.join(
              ''
            )}`
          )
        }
        return true
      }
    },
    disabled: Boolean
  },
  emits: ['click'],
  setup(props: IButtonProps, ctx: any) {
    const classify = computed(() => [
      'axe-button',
      `axe-button-${props.type}`,
      props.icon && `axe-button-${props.position}`
    ])

    const handleClick = (e: MouseEvent) => {
      ctx.emit('click', e)
    }
    return {
      classify,
      handleClick
    }
  }
}
